/*

FONT FAMILY


*/

.sans-serif {
  font-family:  -apple-system, BlinkMacSystemFont,
                "Segoe UI", "Roboto", "Oxygen",
                "Ubuntu", "Cantarell", "Fira Sans",
                "Droid Sans", "Helvetica Neue",
                sans-serif;
}


code, .code {
  font-family: Consolas,
               Monaco,
               "Lucida Console",
               "Liberation Mono",
               "DejaVu Sans Mono",
               "Bitstream Vera Sans Mono",
               "Courier New",
               monospace;
}
